import mpvuePicker from '@/components/mpvue-picker/mpvuePicker.vue'
import upload from '@/components/upload'
import { pathToBase64, base64ToPath } from '@/common/js/base64.js'
import { postDemand } from '@/api'
import { uploadImg, delUploadImg, hideTabbarButton } from '@/common/js/common'
import { mapState, mapActions, mapMutations } from 'vuex'
export default {
	name: '',
	data() {
		return {
			demandType: '选择需求类型',
			demandId: '',
			title: '',
			desc: '',
			imgList: [],
			baseList: []
		}
	},
	onLoad(options) {
		if(options.id) {
			this.demandType = options.name;
			this.demandId = options.id
		}	
	},
	computed: {
		...mapState(['sort_type']),
	},
	onBackPress (e) {
		// uni.switchTab({
		// 	url: '/pages/publish/index/index'
		// })
		// return true;
	},
	onShow () {
		hideTabbarButton()
	},
	methods: {
		...mapMutations(['SORT_TYPE']),
		async onPostDemandEvent () {   // 提交发布
			if(this.title == '') {
				uni.showToast({
					title: '请输入需求标题',
					icon: 'none'
				})
				return;
			}
			if(this.desc == '') {
				uni.showToast({
					title: '请输入需求描述',
					icon: 'none'
				})
				return;
			}
			if(this.baseList.length <=0) {
				uni.showToast({
					title: '请至少上传一张图片',
					icon: 'none'
				})
				return;
			}
			let result = await postDemand ({
				token: uni.getStorageSync('token'),
				sid: this.sort_type.id,
				title: this.title,
				content: this.desc,
				images: this.baseList.join('|')
			})
			if(result.code == 200) {
				uni.showToast({
					title: '需求发布成功'
				})
			}
			setTimeout(() => {
				uni.navigateBack()
			}, 1500)
		},
		onChoseTypeClick() { // 选择需求类型
			// uni.navigateBack()
			uni.navigateTo({
				url: '/pages/home/subPages/publish-type/publish-type?type=1'
			})
		},
		onAddImgEvent () {  // 上传图片
			let self = this;
			uploadImg(self.baseList, self.imgList)
		},
		onDelImgEvent (index) {
			let self = this;
			delUploadImg(self.baseList, self.imgList, index)
		}
	},
	components: {
		mpvuePicker,
		upload
	}
}
